import React, { Component } from "react";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import ReactSearchAutocomplete from "react-search-autocomplete/dist/ReactSearchAutocomplete";
import { getCetakUlang } from "../actions/cetakUlangAction";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptoString = (state) => {
  return {
    listCetak: state.provinsi.listCetak,
  };
};

class CetakUlang extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
          sort: true,
        },
        {
          dataField: "supplier",
          text: "Supplier",
          sort: true,
        },
        {
          dataField: "noNota",
          text: "Nomor Nota",
          sort: true,
        },
        {
          dataField: "noTerima",
          text: "Nomor Terima",
          sort: true,
        },
        {
          dataField: "bruto",
          text: "Brutto (Gr)",
          align: "right",
          headerAlign: "right",
          sort: true,
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          align: "right",
          headerAlign: "right",
          sort: true,
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          align: "right",
          headerAlign: "right",
          sort: true,
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          align: "right",
          headerAlign: "right",
          sort: true,
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => console.log("SUCCESS")}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print Ulang
                </button>
              </div>
            );
          },
        },
      ],
      isi: [
        {
          netto: "23",
          noTerima: "11",
        },
      ],
      startingDate: formatedDate,
      jenis: "",
    };
  }

  handleOnSearch = (data) => {
    this.setState({
      jenis: data,
    });
  };

  componentDidMount() {
    this.props.dispatch(getCetakUlang());
  }

  render() {
    switch (this.state.jenis) {
      case "Cetak Ulang Bukti Penerimaan Barang Supplier":
        return (
          <>
            <div className="col-lg-6 mb-3" style={{ zIndex: 999 }}>
              <label htmlFor="">Tuliskan Jenis Transaksi</label>
              <ReactSearchAutocomplete
                items={this.props.listCetak}
                onSelect={(data) => this.handleOnSearch(data)}
                autoFocus
                styling={{ borderRadius: "10px" }}
              />
            </div>
            <div className="col-lg-12 col-md-12">
              {this.state.jenis}
              <div className="col-lg-12 col-md-12">
                <ToolkitProvider
                  keyField="id"
                  data={this.state.isi}
                  columns={this.state.columns}
                  search
                >
                  {(props) => (
                    <div className="form-group">
                      <div className="text-right mt-5 mb-3">
                        <SearchBar
                          {...props.searchProps}
                          className="form-control"
                          placeholder="Pencarian"
                          style={{ margin: 0, top: "50%" }}
                        />
                      </div>
                      <BootstrapTable
                        {...props.baseProps}
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                      <ExportCSVButton
                        {...props.csvProps}
                        className="btn btn-primary"
                      >
                        Export CSV
                      </ExportCSVButton>
                    </div>
                  )}
                </ToolkitProvider>
              </div>
            </div>
          </>
        );
      default:
        break;
    }
  }
}

export default connect(maptoString, null)(CetakUlang);
